/* grid */
@use '../abstracts/tools' as *;
@use '../abstracts/variables/root' as r;

.row {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  row-gap: 20px;
  margin-bottom: 20px;
  @include screen(md) {
    flex-direction: row;
    gap: 20px;
  }
  &.row--green .col {
    @include setBG(clr(green));
    padding: 30px;
  }
  &.row--red .col {
    @include setBG(clr(red, dark));
    padding: 30px;
  }
}

.a {
  &-start {
    align-items: flex-start;
  }
  &-end {
    align-items: flex-end;
  }
  &-center {
    align-items: center;
  }
  &-baseline {
    align-items: baseline;
  }
}

.j {
  &-start {
    justify-content: flex-start;
  }
  &-end {
    justify-content: flex-end;
  }
  &-center {
    justify-content: center;
  }
  &-between {
    justify-content: space-between;
  }
  &-around {
    justify-content: space-around;
  }
  &-even {
    justify-content: space-evenly;
  }
}

.col {
  flex: 1;
  &.col--red {
    @include setBG(clr(red, dark));
    padding: 30px;
  } 
  &.col--green {
    @include setBG(clr(green, dark));
    padding: 30px;
  } 
  &.col--orange {
    @include setBG(clr(orange, lightest));
    padding: 30px;
  }      
}

